.ingredient-search-window {
  display: flex;
  width: 37%;
  height: 100%;
  line-height: 100%;
  margin: 0 0 0.5rem;
  position: relative;
  font-size: 1rem;
  border-left: 3px solid black;
  border-right: 3px solid black;
}

.ingredient-search-window .ingredient-search {
  width: 60%;
  position: relative;
  border-right: 3px solid black;
}

.ingredient-search-window .floating-contents .floating-btn {
  width: 2rem;
  height: 100%;
}

.ingredient-search-window .floating-contents .ingredients-menu {
  width: 100%;
  background-color: white;
  position: absolute;
  top: 3rem;
  display: flex;
  flex-direction: column;
  visibility: hidden;
  z-index: 2;
}
.ingredient-search-window .floating-contents .ingredients-menu.clicked {
  visibility: visible;
}

.ingredient-search-window .ingredients-menu > .first-level-item {
  width: 100%;
  height: 2.5rem;
  line-height: 2.5rem;
  padding-left: 1.5rem;
  border-bottom: 1px solid black;
}
.ingredient-search-window
  .ingredients-menu
  > .first-level-item
  > .ingredient-name {
  width: 50%;
}

.ingredient-search-window .ingredients-menu .second-level-items {
  visibility: hidden;
  width: 50%;
  position: absolute;
  top: 0;
  left: 50%;
}
.ingredient-search-window
  .ingredients-menu
  .second-level-items
  > .second-level-item {
  width: 100%;
}

.ingredient-search-window .ingredients-menu .focused {
  font-weight: bold;
  color: red;
}
.ingredient-search-window .ingredients-menu .selected {
  visibility: visible;
}

.ingredient-search-window .floating-contents .grey-screen {
  width: 1536px;
  min-height: 816px;
  top: 48px;
  left: -229px;
  position: absolute;
  background-color: grey;
  z-index: 1;
  opacity: 50%;
  visibility: hidden;
}
.ingredient-search-window .floating-contents .grey-screen.clicked {
  /*클래스 두 개 붙여야 인식함*/
  visibility: visible;
}

.ingredient-search-window .search-window-label #searchWindow {
  margin: 0 0 0.5rem;
}

.ingredient-search-window .ingredient-use-probability {
  width: 35%;
  position: relative;
}
