.food-filter .filter-btn {
  width: 88px;
  height: 1.5rem;
  display: inline-block;
  color: gray;
  font-size: 1rem;
  font-weight: bold;
  transition: all 0.25s;
  flex-grow: 1;
}
.food-filter.cooking-ingredients .filter-btn:nth-child(6),
.food-filter.cooking-methods .filter-btn:nth-child(7) {
  font-size: 0.875rem;
}

.food-filter .filter-btn.btn-clicked {
  color: red;
  font-weight: bold;
  transition: all 0.25s;
}
.food-filter .filter-btn:not(.btn-clicked):hover {
  color: black;
  font-weight: bold;
  transition: all 0.1s;
}

.food-filter.tastes .filter-btn {
  width: 1.5rem;
  height: 1.5rem;
}
