.seleceted-ingredient-group {
  width: 35%;
  gap: 1.5rem;
  overflow: hidden;
  border-left: 3px solid black;
}

.seleceted-ingredient-group .scroll {
  display: flex;
  height: 100%;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  /*해당 속성이 있어야, 하위 요소의 텍스트 노드들이 
  해당 노드의 영역을 넘어가도 1한줄로 계속 이어지는 듯*/
}
.seleceted-ingredient-group .scroll::-webkit-scrollbar {
  display: none;
}
.seleceted-ingredient-group .scroll .scroll-placeholder {
  width: 100%;
  text-align: center;
  font-style: italic;
}

.seleceted-ingredient-group .scroll .search-filter-item {
  /* width: 25%; */
  padding: 0 1rem;
  border-right: 1px solid black;
  /* box-sizing: border-box; */
}
