.search-result-header {
  display: flex;
  width: 100%;
  height: 4rem;
  line-height: 4rem;

  border: 3px solid black;
  justify-content: space-between;
}
.search-result-header .page-title {
  width: 15%;
  font-size: 2rem;
}
.search-result-main .food-filters {
  width: 100%;
  height: 300px;
  grid-template-columns: 1fr 1.5fr 2.5fr 2.5fr 2.5fr;
  grid-template-rows: 100%;
  grid-template-areas: "menuTypes dishStyles cookingIngredients cookingMethods tastes";
}
.search-result-main .food-filters .food-filter.dish-styles {
  border-right: 3px solid black;
  border-top: 0;
}
.search-result-main .food-filters .food-filter.tastes {
  flex-direction: row;
}
.search-result-main .food-filters .food-filter.tastes .taste:nth-child(n + 2) {
  border-top: 0;
  border-left: 3px solid black;
}
.search-result-main .filter-btns {
  padding: 1rem 1rem 0;
}
.food-filter.tastes .taste .filter-btns {
  padding: 1rem 1.5rem 1rem;
}
.search-result-main .menu-types .filter-btn {
  height: 1.25rem;
}
.search-result-main .menu-types .filter-btn:last-child {
  display: none;
}
