.food-detail-page-header {
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid black;
  border-left: 1px solid black;
  border-right: 1px solid black;
}
.food-detail-page-header .page-title {
  font-size: 2rem;
}
.food-detail-page-header .back-btn {
  font-size: 1rem; /*TODO: 제거 예정*/
}

.food-detail-page-main {
  width: 100%;
  height: 756px;
  padding: 3rem 5% 0; /*margin은 box-sizing: border-box에 포함 x so 재측정 필요*/
  border-left: 1px solid black;
  border-right: 1px solid black;
}
.food-detail-page-main .food-description {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-between;
  flex-wrap: wrap;
}
.food-detail-page-main .food-description .food-description-title,
.food-detail-page-main .food-description .food-description-detail,
.food-detail-page-main .food-description .food-description-tastes,
.food-detail-page-main .food-description .food-ingredients-use-probability,
.food-detail-page-main .food-description .food-ingredients-needed,
.food-detail-page-main .food-description .food-recipe-sites {
  height: 50%;
}
.food-detail-page-main .food-description .food-description-title,
.food-detail-page-main .food-description .food-ingredients-use-probability {
  width: 30%;
}
.food-detail-page-main .food-description .food-description-detail,
.food-detail-page-main .food-description .food-ingredients-needed {
  width: 40%;
}
.food-detail-page-main .food-description .food-description-tastes,
.food-detail-page-main .food-description .food-recipe-sites {
  width: 30%;
}
.food-detail-page-main .food-description .food-description-title {
  text-align: center;
}
.food-detail-page-main .food-description-title .food-name {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
  text-decoration: underline;
}
.food-detail-page-main .food-description-title .food-image {
  width: 100%;
}
.food-detail-page-main .food-description-title .food-image img {
  width: 250px;
  height: 250px;
  border: 1px solid black;
}

.food-detail-page-main .food-description-detail .description-detail-item,
.food-detail-page-main .food-description-tastes .description-tastes-item {
  width: 100%;
  padding: 10% 0 0 10%;
  font-size: 0;
}
.food-detail-page-main .description-detail-item .description-detail-title,
.food-detail-page-main .description-tastes-item .description-tastes-title {
  display: inline-block;
  font-size: 1.5rem;
  font-weight: bold;
  margin-right: 1rem;
}
.food-detail-page-main .description-detail-item .description-detail-content,
.food-detail-page-main .description-tastes-item .description-tastes-content {
  display: inline-flex;
  font-size: 1rem;
  font-weight: bold;
  text-decoration: underline;
  gap: 1rem;
}
.food-detail-page-main .food-description .description-item {
  text-align: center;
  padding: 2%;
}
.food-detail-page-main .food-description .description-item-title {
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: underline;
  margin-bottom: 1.5rem;
}

.food-detail-page-main
  .food-ingredients-use-probability
  .probability-graph-content {
  width: 100%;
  padding: 0 10%;
  height: 80%;
}
.food-detail-page-main
  .food-ingredients-use-probability
  .probability-graph-content
  .graph {
  width: 100%;
  height: 50%;
}

.food-detail-page-main .food-ingredients-needed .ingredients-list-content {
  width: 100%;
  height: 80%;
  overflow: hidden;
}
.food-detail-page-main .food-ingredients-needed .ingredients-list-content ul {
  width: 100%;
  height: 100%;
  padding: 0 2.5%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  overflow-y: scroll;
}
.food-detail-page-main
  .food-ingredients-needed
  .ingredients-list-content
  ul::-webkit-scrollbar {
  display: none;
}

.food-detail-page-main .food-ingredients-needed .ingredients-list-content li {
  width: 30%;
  height: 1rem;
  line-height: 1rem;
  padding: 0 0.5rem;
  display: flex;
  justify-content: space-between;
}

.food-detail-page-main .food-recipe-sites .recipe-site-content {
  width: 100%;
  padding-left: 35%;
}
.food-detail-page-main .food-recipe-sites .recipe-site-content ul {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 2rem 0;
  list-style: disc;
}
.food-detail-page-main .food-recipe-sites .recipe-site-content li {
  width: 100%;
  text-align: left;
  /* position: relative; */
}
