.food-filters {
  width: 1250px;
  height: 500px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 45% 55%;
  grid-template-areas:
    "menuTypes cookingIngredients cookingMethods tastes"
    "dishStyles cookingIngredients cookingMethods tastes";
  border: 3px solid black;
}

.food-filters .food-filter {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.food-filters .food-filter.menu-types {
  grid-area: menuTypes;
  border-right: 3px solid black;
}
.food-filters .food-filter.dish-styles {
  grid-area: dishStyles;
  border-right: 3px solid black;
  border-top: 3px solid black;
}

.food-filters .food-filter.cooking-ingredients {
  grid-area: cookingIngredients;
  border-right: 3px solid black;
}
.food-filters .food-filter.cooking-methods {
  grid-area: cookingMethods;
  border-right: 3px solid black;
}
.food-filters .food-filter.tastes {
  grid-area: tastes;
  justify-content: space-between;
}

.food-filters .food-filter.tastes .taste:nth-child(n + 2) {
  border-top: 3px solid black;
}
