.search-window-label {
  position: relative;
}

.search-window-label input {
  display: block;
  width: 100%;
  height: 100%;
  padding: 0 2rem;
  border: 0;
  position: relative;
  text-align: center;
}
.search-window-label input:focus,
.search-window-label input.have-text {
  color: black;
  background-color: white;
  z-index: 2;
}

.search-window-label input:focus::placeholder {
  color: transparent;
}
