.unified-search-window {
  display: block;
  height: 3rem;
  width: 1000px;
  margin: 5rem auto 3rem;
  position: relative;
  border-bottom: 1px solid black;
  font-size: 1rem;
}

.unified-search-window .floating-contents {
  padding: 0 1rem; /*margin을 주면 absolute position의 노드들의 위치를 잡기 헷갈림*/
  display: flex;
  justify-content: space-between;
}
.unified-search-window .floating-contents .floating-btn {
  position: relative;
}
