.searched-foods {
  width: 100%;
  height: 30rem;
  padding: 2rem;
  border-right: 3px solid black;
  border-left: 3px solid black;
  overflow: hidden;
  gap: 1rem;
  position: relative;
}
.searched-foods .searched-foods-number {
  position: absolute;
  top: 0.5rem;
  left: 7%;
}
.searched-foods .searched-foods-number b {
  text-decoration: underline;
}

.searched-foods .containter {
  display: flex;
  width: 100%;
  height: 100%; /*스크롤 발생을 위해서는 치수가 정해저 있어야 함*/
  flex-wrap: wrap;
  overflow: scroll;
  /* white-space: nowrap; 텍스트에 적용하는게 아닌 flex의 자식인 block에 적용하기 때문에*/
}
.searched-foods .containter::-webkit-scrollbar {
  display: none;
}

.searched-foods .containter li {
  width: 20%;
  height: 50%;
  /* border-right: 1px solid black; */
  /* border-bottom: 1px solid black; */
  text-align: center;
  list-style: none;
}
/* .searched-foods .containter li:nth-child(5n) {
  border-right: 0;
} */

.searched-foods .containter .food-image {
  width: 50%;
  height: 50%;
}
