.floating-contents .recommended-menu-type,
.floating-contents .menu-ranking-items {
  line-height: 3rem;
}
.floating-contents .recommended-menu-type {
  font-size: 1.5rem;
  font-weight: bold;
}
.floating-contents .menu-ranking-items {
  width: 60%;
  display: flex;
  justify-content: space-around;
}
.floating-contents .menu-ranking-items .menu-ranking-item {
  width: 15%;
  color: gray;
  position: relative;
  z-index: 1;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  transition: all 0.25;
}
.floating-contents .menu-ranking-items .menu-ranking-item:hover {
  color: red;
  font-weight: bold;
  transition: all 0.25s;
}
.floating-contents .menu-ranking-items .menu-ranking-item button {
  width: 100%;
}
